<script>
import backgroundImage from "@/assets/DailyOperation/background/1-1.jpg";
import LineChartLimit from "@/components/LineChartLimit";
import websockObject from "@/mixins/websock";
const tickcount = 20;
export default {
  name: "CapacityLineChart",
  mixins: [websockObject],
  data() {
    return {
      // eslint-disable-next-line no-undef
      title: dailyLeftHeader.title,
      // eslint-disable-next-line no-undef
      orderList: dailyLeftHeader.order,
      fristBack: true,
      backgroundImage,
      CapacityLineS: [],
      useTheTestDateTime: 0
    };
  },
  components: {
    LineChartLimit
  },
  mounted() {
    // eslint-disable-next-line no-undef
    this.wsuri = BaseUrl.dailyLeftHeader.websockUrl;
    // this.useTheTestDate()
  },
  methods: {
    useTheTestDate() {
      this.useTheTestDateTime = this.useTheTestDateTime + 1;
      let tillTiem = new Date().getTime();
      let m = 10000;
      let n = 0;
      let waiwei = [
        {
          dateTime: tillTiem,
          type: "all",
          limit: 0,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "外围1",
          group: "零售_外围"
        },
        {
          dateTime: tillTiem,
          type: "all",
          limit: 0,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "外围2",
          group: "零售_外围"
        },
        {
          dateTime: tillTiem,
          type: "all",
          limit: 0,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "外围3",
          group: "零售_外围"
        },
        {
          dateTime: tillTiem,
          type: "all",
          limit: 0,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "外围4",
          group: "零售_外围"
        }
      ];
      let KCXP = [
        {
          dateTime: tillTiem,
          type: "all",
          limit: 0,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "KCXP1",
          group: "零售_KCXP"
        }
        // {'dateTime': tillTiem, 'type': 'all', 'limit': 0, 'value': Math.floor(Math.random() * (m - n) + n), 'indicateName': 'KCXP2', 'group': '零售_KCXP'}
      ];
      let KCBP = [
        {
          dateTime: tillTiem,
          type: "all",
          limit: 0,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "KCBP1",
          group: "零售_KCBP"
        }
      ];
      if (this.useTheTestDateTime > 10) {
        KCBP.push({
          dateTime: tillTiem,
          type: "all",
          limit: 0,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "KCBP2",
          group: "零售_KCBP"
        });
      }
      let daseBase = [
        {
          dateTime: tillTiem,
          type: "all",
          limit: 0,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "数据库1",
          group: "零售_数据库"
        },
        {
          dateTime: tillTiem,
          type: "all",
          limit: 0,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "数据库2",
          group: "零售_数据库"
        }
      ];
      let test = [
        {
          dateTime: tillTiem,
          type: "all",
          limit: 0,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "线路1",
          group: "零售_线路"
        },
        {
          dateTime: tillTiem,
          type: "all",
          limit: 0,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "线路2",
          group: "零售_线路"
        },
        {
          dateTime: tillTiem,
          type: "all",
          limit: 0,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "线路3",
          group: "零售_线路"
        },
        {
          dateTime: tillTiem,
          type: "all",
          limit: 0,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "FS网关1",
          group: "零售_FS网关"
        },
        {
          dateTime: tillTiem,
          type: "all",
          limit: 0,
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "FS网关2",
          group: "零售_FS网关"
        }
      ];
      test = [...test, ...waiwei, ...KCXP, ...KCBP, ...daseBase];
      this.getWebsocketMsg(test);
      let _this = this;
      this.timeOut && clearTimeout(this.timeOut);
      this.timeOut = setTimeout(() => {
        _this.useTheTestDate();
      }, 2000);
    },
    getWebsocketMsg(sourDate) {
      let self = this;
      if (sourDate.length > 0) {
        sourDate.forEach(element => {
          element.group = element.group
            .replace(/\s+/g, "")
            .replace("零售_", "")
            .toUpperCase();
          let target = self.CapacityLineS.findIndex(item => {
            return item.group === element.group;
          });
          if (target === -1) {
            let item = {
              indicateList: [
                {
                  indicateName: element.indicateName,
                  value: []
                }
              ],
              group: element.group
            };
            item.indicateList[0].value.push(element);
            self.CapacityLineS.push(item);
          } else {
            let item = self.CapacityLineS[target];
            let isIndicateNameSet = item.indicateList.findIndex(
              indicateitem => {
                return indicateitem.indicateName === element.indicateName;
              }
            );
            if (isIndicateNameSet === -1) {
              let indicate = {
                indicateName: element.indicateName,
                value: []
              };
              indicate.value.push(element);
              item.indicateList.push(indicate);
              item.indicateList.sort((a, b) => {
                return (
                  Number(a.indicateName.replace(/[^\d]/g, " ")) -
                  Number(b.indicateName.replace(/[^\d]/g, " "))
                );
              });
            } else {
              let base = item.indicateList[isIndicateNameSet].value;
              base.push(element);
              item.indicateList[isIndicateNameSet].value = base.slice(
                -1 * tickcount
              );
            }
            self.$set(self.CapacityLineS, target, item);
          }
        });
      }
      return new Promise(function(resolve, reject) {
        resolve();
      });
    }
  },
  render(h) {
    return (
      <section
        class="mainBox"
        {...{
          style: { backgroundImage: " url(" + this.backgroundImage + ")" }
        }}
      >
        <section class="mainChartBox">
          <div class="title mainTitile">{this.title}</div>
          <section class="LineChartBox">
            <section class="contentBox">
              {this.CapacityLineS.map((item, index) => {
                return (
                  <div
                    class="LineChartItem"
                    key={"CapacityLineS" + index + item.indicateList.length}
                    {...{
                      style: {
                        order: this.orderList.indexOf(item.group),
                        width: "283px"
                      }
                    }}
                  >
                    {item.indicateList.map(indicateItem => {
                      return (
                        <LineChartLimit
                          sourDate={indicateItem.value}
                          title={indicateItem.indicateName}
                        ></LineChartLimit>
                      );
                    })}
                  </div>
                );
              })}
            </section>
          </section>
        </section>
      </section>
    );
  }
};
</script>

<style scoped>
.mainChartBox {
  width: 1833px;
  height: 765px;
  position: absolute;
  bottom: 0;
  left: 60px;
  display: flex;
  flex-flow: column;
}
.title {
  position: relative;
  width: auto;
  font-size: 30px;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 2px;
  line-height: 1;
  text-align: left;
  left: 0;
  top: 0;
  padding: 25px;
}
.LineChartBox {
  width: 100%;
  flex: auto;
}
.contentBox {
  width: 1786px;
  height: 100%;
  display: flex;
  margin: auto;
}
.LineChartItem {
  width: 100%;
  height: 630px;
  display: flex;
  flex-flow: column;
  padding: 0;
  padding-left: 15px;
  padding-top: 15px;
}
.LineChartName {
  text-align: left;
  width: 100%;
  margin: auto;
  margin-left: 2%;
}
.LineChartLimit {
  height: 100%;
}
</style>



